import React from 'react'

class Buy extends React.Component{


    render(){
        return (
            <div className="buy">
                <h3 className="heading-3 buy__heading-3">Oto Twoje zamowienie</h3>
                <p className="buy__paragraph">Sprawdz czy wszystko się zgadza</p>
            </div>
        )
    }
}

export default Buy


